<style scoped>
</style>
<template>
  <div class="haeder-menu">
    <Modal
        title="新建类别"
        v-model="showCategory"
        :mask-closable="false"
        @on-ok="categoryOk"
        @on-cancel="ategoryCancel">
        <p>Content of dialog</p>
        <p>Content of dialog</p>
        <p>Content of dialog</p>
    </Modal>
    <MenuItem name="2">
      <Icon type="ios-home-outline" size="20" />
      首页
    </MenuItem>
    <Submenu name="3">
      <template slot="title">
        <Icon type="ios-add" size="20"/>
        新建
      </template>
      <MenuItem name="3-1">类别</MenuItem>
      <MenuItem name="3-2">文章</MenuItem>
    </Submenu>
  </div>
</template>
<script>
export default {
  data () {
    return {
      menuSelectEventName: 'BlogHeader-select',
      showCategory: false
    }
  },
  methods: {
    menuSelect (name) {
      switch (name) {
        case '3-1':
          this.addCategory()
          break
        case '3-2':
          this.addArticle()
          break
        default:
          break
      }
    },
    addCategory () {
      this.showCategory = true
    },
    categoryOk () {
      console.info('categoryOk')
      this.showCategory = false
    },
    ategoryCancel () {
      console.info('ategoryCancel')
      this.showCategory = false
    },
    addArticle () {
      console.info('add article')
    }
  },
  created () {
    document.title = this.$const.moduleTitles.blog
    window.eventBus.$on(this.menuSelectEventName, this.menuSelect)
  },
  destroyed () {
    window.eventBus.$off(this.menuSelectEventName, this.menuSelect)
  }
}
</script>



