<style scoped>
.done {
  color: #c5c8ce;
}
</style>
<template>
  <div class="efficient-project-info" :style="{minHeight: containerHeight, maxHeight: containerHeight, overflow:'auto'}">
    <Modal
      :loading="true"
      title="更新项目"
      v-model="showUpdateProject"
      :mask-closable="false"
      @on-ok="updateProject">
      <Form ref="updateProjectForm" :model="updateProjectForm" :rules="updateProjectValidate" :label-width="80">
        <FormItem label="项目名称" prop="name">
          <Input v-model="updateProjectForm.name" placeholder="请输入项目名称..."/>
        </FormItem>
        <FormItem label="项目描述" prop="description">
          <Input v-model="updateProjectForm.description" type="textarea" :autosize="{minRows: 5,maxRows: 15}" placeholder="请输入项目描述..."/>
        </FormItem>
      </Form>
    </Modal>
    <Breadcrumb style="margin-bottom: 10px;">
      <BreadcrumbItem to="/efficient">首页</BreadcrumbItem>
      <BreadcrumbItem>项目详情</BreadcrumbItem>
      <BreadcrumbItem>{{project.name}}</BreadcrumbItem>
    </Breadcrumb>
    <Row style="padding-top: 10px">
      <Row style="margin-bottom: 10px;">
        <Col span="18" offset="2">
          <Steps :current="project.status ? project.status + 1 : 1">
            <Step title="新建" :content="project.createAt"></Step>
            <Step title="进行中" content=""></Step>
            <Step title="已完成" :content="project.status === 1 ? project.endAt : ''"></Step>
          </Steps>
        </Col>
      </Row>
      <Row :style="{
        minHeight: projectBoxHeight,
        maxHeight: projectBoxHeight,
        overflow: 'auto'}">
        <Col span="18" offset="2">
          <Form :label-width="80">
            <FormItem label="项目名称："><strong>{{project.name}}</strong></FormItem>
            <div v-if="!project.parentId">
              <FormItem label="项目类型："><p>{{project.type}} [ {{project.category}} ]</p></FormItem>
            </div>
            <div v-if="project.parentId">
              <FormItem label="项目类型："><p>{{project.type}}</p></FormItem>
              <FormItem label="所属项目："><p><a href="#" @click.prevent="projectInfo(project.parentId)">{{project.parentName}}</a></p></FormItem>
            </div>
            <FormItem label="项目描述："><p>{{project.description}}</p></FormItem>
            <FormItem label="任务进度：">
              <Timeline>
                <div v-for="iteration in iterations" :key="iteration.id">
                  <TimelineItem color="green">
                    <Icon type="ios-trophy" slot="dot"></Icon>
                    <span>
                      <Poptip trigger="hover" :title="iteration.name" :content="iteration.description">
                        {{iteration.createAt}}
                        <a v-if="!iteration.isProject" @click.prevent="projectInfo(iteration.id)"><span>{{iteration.name}}</span></a>
                        <span v-if="iteration.isProject">{{iteration.name}}</span>
                      </Poptip>
                    </span>
                  </TimelineItem>
                  <TimelineItem v-for="task in iteration.tasks" :key="task.id" :color="task.status === 1 ? 'green' : 'gray'">
                    <Poptip confirm
                      word-wrap
                      width="200"
                      trigger="hover"
                      :title="task.description"
                      :content="task.description"
                      @on-ok="doneTask(task.id)"
                      @on-cancel="undoneTask(task.id)"
                      ok-text="已完成"
                      cancel-text="未完成">
                      <span :class="task.status === 1 ? 'done' : ''">{{task.createAt}}  {{task.name}}</span>
                    </Poptip>
                    <Poptip confirm
                      word-wrap
                      width="200"
                      title="确定删除吗？"
                      @on-ok="deleteTask(task.id, iteration.id)"
                      ok-text="删除"
                      cancel-text="取消">
                      <Icon type="ios-trash-outline" />
                    </Poptip>
                  </TimelineItem>
                  <TimelineItem v-if="iteration.more"><a href="#" @click.prevent="moreTasks(iteration.id)">查看更多</a></TimelineItem>
                </div>
                <TimelineItem v-if="project.status === 1" color="green">
                  <Icon type="ios-trophy" slot="dot"></Icon>
                  <span>{{project.endAt}} 完成</span>
                </TimelineItem>
              </Timeline>
            </FormItem>
          </Form>
        </Col>
      </Row>
      <Row style="min-height: 50px;max-height:50px;overflow: 'auto';padding-top:10px;">
        <Col span="10" offset="12">
          <Button v-if="project.status === 1" type="warning" @click="updateProjectStatus(0)">未完成</Button>
          <Button v-if="project.status === 0" type="success" @click="updateProjectStatus(1)">已完成</Button>&nbsp;&nbsp;&nbsp;&nbsp;
          <Button type="info" @click="showUpdateProjectModel">更新</Button>&nbsp;&nbsp;&nbsp;&nbsp;
          <Poptip confirm
            word-wrap
            width="200"
            title="确定删除吗？相关联的日常及任务全部删除！！！"
            @on-ok="projectDelete"
            ok-text="删除"
            cancel-text="取消">
            <Button type="error">删除</Button>
          </Poptip>
        </Col>
      </Row>
    </Row>
  </div>
</template>
<script>
export default {
  data () {
    return {
      showUpdateProject: false,
      updateProjectForm: {
        id: '',
        name: '',
        description: ''
      },
      updateProjectValidate: {
        name: [{ required: true, message: '项目名称不能为空', trigger: 'blur' }],
        description: [
          { required: true, message: '项目描述不能为空', trigger: 'blur' },
          { type: 'string', max: 200, message: '描述最大长度不得超过200个字', trigger: 'blur' }
        ]
      },
      containerHeight: '700px',
      projectBoxHeight: '500px',
      project: {},
      iterations: []
    }
  },
  methods: {
    loadProject (id) {
      this.$Loading.start()
      this.$http.post(this.$const.efficient.api.project_info + id, {}).then(res => {
        if (res.code !== 0) {
          this.$Loading.error()
          return
        }
        this.$Loading.finish()
        this.project = res.data
        const iterations = (res.data.iterations || []).map(item => {
          item['more'] = true
          item['isProject'] = false
          return item
        })
        this.iterations = [{
          id: id,
          createAt: this.project.createAt,
          name: this.project.name,
          description: this.project.description,
          endAt: this.project.endAt,
          status: this.project.status,
          tasks: this.project.tasks,
          more: false,
          isProject: true
        }, ...iterations]
      })
    },
    projectInfo (id) {
      this.$router.push(`/efficient/project/${id}`)
      this.loadProject(id)
    },
    doneTask (id) {
      this.$http.post(this.$const.efficient.api.task_done + id, {}).then(res => {
        if (res.code !== 0) {
          return
        }
        this.$Message.success('好棒，继续加油鸭!')
        this.iterations.forEach(iteration => {
          (iteration.tasks || []).forEach(task => {
            if (task.id === id) {
              task.status = 1
            }
          })
        })
      })
    },
    undoneTask (id) {
      this.$http.post(this.$const.efficient.api.task_undone + id, {}).then(res => {
        if (res.code !== 0) {
          return
        }
        this.iterations.forEach(iteration => {
          (iteration.tasks || []).forEach(task => {
            if (task.id === id) {
              task.status = 0
            }
          })
        })
      })
    },
    deleteTask (taskId, projectId) {
      this.$http.post(this.$const.efficient.api.task_delete + taskId, {}).then(res => {
        if (res.code !== 0) {
          return
        }
        for (let i = 0; i < this.iterations.length; i++) {
          if (this.iterations[i].id === projectId) {
            for (let j = 0; j < this.iterations[i].tasks.length; j++) {
              if (this.iterations[i].tasks[j].id === taskId) {
                this.iterations[i].tasks.splice(j, 1)
              }
            }
          }
        }
      })
    },
    moreTasks (projectId) {
      this.$http.post(this.$const.efficient.api.project_all_task + projectId, {}).then(res => {
        if (res.code !== 0) {
          return
        }
        for (let i = 0; i < this.iterations.length; i++) {
          if (this.iterations[i].id === projectId) {
            const newIteration = Object.assign({}, this.iterations[i], { tasks: res.data, more: false })
            this.$set(this.iterations, i, newIteration)
          }
        }
      })
    },
    updateProjectStatus (status) {
      this.$http.post(this.$const.efficient.api.project_status + this.project.id + '/' + status, {}).then(res => {
        if (res.code !== 0) {
          return
        }
        this.project.status = status
        this.project.endAt = status === 1 ? this.$const.util.newDate() : ''
      })
    },
    projectDelete () {
      this.$http.post(this.$const.efficient.api.project_delete + this.project.id, {}).then(res => {
        if (res.code !== 0) {
          return
        }
        this.$router.push('/efficient')
      })
    },
    showUpdateProjectModel () {
      this.updateProjectForm = {
        id: this.project.id,
        name: this.project.name,
        description: this.project.description
      }
      this.showUpdateProject = true
    },
    updateProject () {
      this.$refs.updateProjectForm.validate((valid) => {
        if (!valid) {
          return
        }
        this.$Loading.start()
        this.$http.post(this.$const.efficient.api.project_update, this.updateProjectForm).then(res => {
          this.showUpdateProject = false
          if (res.code !== 0) {
            this.$Loading.error()
            return
          }
          this.project.name = this.updateProjectForm.name
          this.project.description = this.updateProjectForm.description
          this.$Loading.finish()
          this.$Message.success('更新成功!')
        })
      })
    }
  },
  created () {
    let contentHeight = window.localStorage.getItem('containerHeight')
    this.containerHeight = contentHeight + 'px'
    this.projectBoxHeight = (contentHeight - 146) + 'px'
    this.loadProject(this.$route.params.id)
  }
}
</script>



