<style scoped>
</style>
<template>
  <div class="efficient-setting">
    <Row>
      <Col span="4">
        <Menu theme="light" :active-name="tab" @on-select="select">
          <MenuGroup title="推送设置">
            <MenuItem name="1">
              <Icon type="ios-at-outline" />
              推送地址
            </MenuItem>
            <MenuItem name="2">
              <Icon type="ios-alarm-outline" />
              推送时间
            </MenuItem>
          </MenuGroup>
        </Menu>
      </Col>
      <Col span="20" offset="0">
        <Row>
          <Col v-if="tab === '1'" span="16" offset="2">
            <Alert type="warning" show-icon>
              填写需知
              <template slot="desc">
                这里设置的地址会在需要推送消息时用到，例如设置时间中：生成日报、周报、月报等，还会在新建的定时任务的提醒；<br/>
                推送类型（地址类型）：发送邮件、发送企业微信群机器人消息、发送钉钉群机器人消息；<br/>
                推送地址：邮箱、企业微信群机器人webHookToken、钉钉群机器人webHookToken；<br/>
                测试内容：点击保存按钮时会发送一条测试消息，保障填写地址的有效性；
              </template>
            </Alert>
          </Col>
          <Col v-if="tab === '2'" span="16" offset="2">
            <Alert type="warning" show-icon>
              填写需知
              <template slot="desc">
                这里设置的时间会提前十分钟左右发送到你填写的推送地址上，推送内容为你这里选择的时间单位内完成的任务统计；<br/>
                项目类型：筛选需要推送的项目类型，可以过滤掉不需要统计的项目；<br/>
                时间单位：推送内容统计的单位时间，在该时间单位内完成的内容会得到统计推送；<br/>
                周几：时间单位为每天一次时，可以多选具体周几推送、时间单位为每周一次时，可以指定每周的周几推送；<br/>
                具体时间：消息推送的具体时间点，会在该时间提前十分钟左右发送统计消息；
              </template>
            </Alert>
          </Col>
        </Row>
        <Row style="margin-top:10px;">
          <Col span="12" offset="2">
            <Form v-if="tab === '1'" ref="newPushAddr" :model="newPushAddr" :rules="newPushAddrValidate" :label-width="120">
              <FormItem label="地址类型" prop="type">
                <Select v-model="newPushAddr.type">
                  <Option value="0">邮箱</Option>
                  <Option value="1">企业微信群机器人</Option>
                  <Option value="2">钉钉群机器人</Option>
                </Select>
              </FormItem>
              <FormItem label="推送地址" prop="address">
                <Input v-model="newPushAddr.address" placeholder="请输入推送地址..."/>
              </FormItem>
              <FormItem label="测试内容" prop="test">
                <Input v-model="newPushAddr.test" type="textarea" :autosize="{minRows: 5,maxRows: 15}" placeholder="请输入推送测试内容..."/>
              </FormItem>
              <FormItem>
                <Button type="primary" @click="savePushAddr">保存</Button>
              </FormItem>
            </Form>
            <Form v-if="tab === '2'" ref="newPushTime" :model="newPushTime" :rules="newPushTimeValidate" :label-width="120">
              <FormItem label="项目类型" prop="projectCategories">
                <Select v-model="newPushTime.projectCategories" placeholder="请选择需要推送的项目类型"  multiple style="width:600px">
                  <Option v-for="item in projectCategorys" :value="item.value" :key="item.value">{{ item.label }}</Option>
                </Select>
              </FormItem>
              <FormItem label="时间单位" prop="type">
                <Select v-model="newPushTime.type" @on-change="newPushTime.whichDay = ''" style="width:600px;">
                  <Option value="0">日报（每天一次，统计过去24小时内已完成任务）</Option>
                  <Option value="1">周报（每周一次，统计过去七天内已完成任务）</Option>
                </Select>
              </FormItem>
              <FormItem label="周几" prop="whichDay">
                <Select :multiple="newPushTime.type === '0'" v-model="newPushTime.whichDay" placeholder="请选择周几推送" style="width:600px">
                  <Option v-for="item in whichDays" :value="item.value" :key="item.value">{{ item.label }}</Option>
                </Select>
              </FormItem>
              <FormItem label="具体时间" prop="time">
                <TimePicker v-model="newPushTime.time" type="time" placeholder="请选择具体几点推送" style="width:600px"></TimePicker>
              </FormItem>
              <FormItem>
                <Button type="primary" @click="savePushTime">保存</Button>
              </FormItem>
            </Form>
          </Col>
        </Row>
      </Col>
    </Row>
  </div>
</template>
<script>
export default {
  data () {
    return {
      tab: '1',
      tabObj: {
        value: '1'
      },
      newPushAddr: {
        type: '0',
        address: '',
        test: ''
      },
      newPushAddrValidate: {
        type: [{ required: true, message: '推送类型不能为空', trigger: 'blur' }],
        address: [{ required: true, message: '推送地址不能为空', trigger: 'blur' }],
        test: [
          { required: true, message: '测试内容不能为空', trigger: 'blur' },
          { type: 'string', max: 200, message: '内容最大长度不得超过200个字', trigger: 'blur' }
        ]
      },
      newPushTime: {
        projectCategories: '',
        type: '0', // 每天一次 || 每周一次
        whichDay: '', // 周几（每天一次：多选、每周一次：单选）
        time: '' // 具体几点
      },
      newPushTimeValidate: {
        projectCategories: [{ required: true, type: 'array', min: 1, message: '推送项目类型不能为空', trigger: 'change' }],
        type: [{ required: true, message: '推送时间单位不能为空', trigger: 'blur' }],
        // whichDay: [{ required: true, type: 'array', min: 1, message: '具体周几不能为空', trigger: 'change' }],
        time: [{ required: true, message: '推送时间点不能为空', trigger: 'blur' }]
      },
      projectCategorys: this.$const.constants.projectCategorys,
      whichDays: [{
        value: '1',
        label: '周一'
      }, {
        value: '2',
        label: '周二'
      }, {
        value: '3',
        label: '周三'
      }, {
        value: '4',
        label: '周四'
      }, {
        value: '5',
        label: '周五'
      }, {
        value: '6',
        label: '周六'
      }, {
        value: '7',
        label: '周日'
      }]
    }
  },
  methods: {
    select (name) {
      this.tab = name
      this.$router.push({
        path: '/efficient/setting',
        query: {
          tab: name
        }
      })
      if (this.tab === '1') {
        this.loadAddress()
      }
      if (this.tab === '2') {
        this.loadTime()
      }
    },
    savePushAddr () {
      this.$refs.newPushAddr.validate((valid) => {
        if (!valid) {
          return
        }
        this.$Loading.start()
        this.$http.post(this.$const.efficient.api.push_address_save, this.newPushAddr).then(res => {
          if (res.code !== 0) {
            this.$Loading.error()
            return
          }
          this.$Loading.finish()
          this.$Message.success('保存成功!')
        })
      })
    },
    savePushTime () {
      if (!this.newPushTime.whichDay) {
        this.$Message.error('周几不能为空!')
        return
      }
      this.$refs.newPushTime.validate((valid) => {
        if (!valid) {
          return
        }
        const { projectCategories, type, whichDay, time } = this.newPushTime
        this.$Loading.start()
        this.$http.post(this.$const.efficient.api.push_time_save, {
          projectCategories,
          type,
          whichDay: type === '0' ? '' : whichDay,
          whichDays: type === '1' ? [] : whichDay,
          pushAt: this.$const.util.nowDayTime(time) - this.$const.util.beginDayTime()
        }).then(res => {
          if (res.code !== 0) {
            this.$Loading.error()
            return
          }
          this.$Loading.finish()
          this.$Message.success('保存成功!')
        })
      })
    },
    loadAddress () {
      this.$http.post(this.$const.efficient.api.push_address_get, {}).then(res => {
        if (res.code === 0 && res.data) {
          this.newPushAddr = res.data
          this.newPushAddr.type = res.data.type + ''
        }
      })
    },
    loadTime () {
      this.$http.post(this.$const.efficient.api.push_time_get, {}).then(res => {
        if (res.code === 0 && res.data) {
          this.newPushTime = {
            projectCategories: res.data.projectCategories.map(item => item + ''),
            type: res.data.type + '',
            whichDay: res.data.type === 1 ? res.data.whichDay + '' : res.data.whichDays, // 周几（每天一次：多选、每周一次：单选）
            time: this.$const.util.getTimeStr(res.data.pushAt) // 具体几点
          }
        }
      })
    }
  },
  created () {
    const tab = this.$router.currentRoute.query.tab + ''
    if (tab === '1') {
      this.loadAddress()
    }
    if (tab === '2') {
      this.loadTime()
    }
  }
}
</script>



