<style scoped>
</style>
<template>
  <div class="haeder-menu">
    <Modal
      :loading="true"
      title="新建项目"
      v-model="showProject"
      :mask-closable="false"
      @on-ok="projectOk"
      @on-cancel="projectCancel">
      <Form ref="newProjectForm" :model="newProjectForm" :rules="newProjectValidate" :label-width="80">
        <FormItem label="项目类型" prop="category">
          <Select v-model="newProjectForm.category">
            <Option v-for="item in projectCategorys" :value="item.value" :key="item.value">{{ item.label }}</Option>
            <!-- <Option value="0">公司项目</Option>
            <Option value="1">学习计划</Option>
            <Option value="2">个人规划</Option>
            <Option value="100">其他</Option> -->
          </Select>
        </FormItem>
        <FormItem label="项目名称" prop="name">
          <Input v-model="newProjectForm.name" placeholder="请输入项目名称..."/>
        </FormItem>
        <FormItem label="项目描述" prop="description">
          <Input v-model="newProjectForm.description" type="textarea" :autosize="{minRows: 5,maxRows: 15}" placeholder="请输入项目描述..."/>
        </FormItem>
      </Form>
    </Modal>
    <Modal
      :loading="true"
      title="新建日常"
      v-model="showIteration"
      :mask-closable="false"
      @on-ok="iterationOk"
      @on-cancel="iterationCancel">
        <Form ref="newIterationForm" :model="newIterationForm" :rules="newIterationValidate" :label-width="80">
        <FormItem label="所属项目" prop="parentId">
          <Select v-model="newIterationForm.parentId" placeholder="请选择所属项目...">
            <Option v-for="project in allProject" :value="project.value" :key="project.value">{{project.desc}}</Option>
          </Select>
        </FormItem>
        <FormItem label="日常名称" prop="name">
          <Input v-model="newIterationForm.name" placeholder="请输入日常名称..."/>
        </FormItem>
        <FormItem label="日常描述" prop="description">
          <Input v-model="newIterationForm.description" type="textarea" :autosize="{minRows: 5,maxRows: 15}" placeholder="请输入日常描述..."/>
        </FormItem>
      </Form>
    </Modal>
    <MenuItem name="2">
      <Icon type="ios-home-outline" size="20" />
      首页
    </MenuItem>
    <Submenu name="3">
      <template slot="title">
        <Icon type="ios-add" size="20"/>
        新建
      </template>
      <MenuItem name="3-1"><Icon type="ios-trophy-outline" size="20" />&nbsp;项目</MenuItem>
      <MenuItem name="3-2"><Icon type="ios-umbrella-outline" size="20" />&nbsp;日常</MenuItem>
    </Submenu>
    <Submenu name="4">
      <template slot="title">
        <Icon type="ios-stats" />
        管理
      </template>
      <MenuItem name="4-1"><Icon type="ios-time-outline" size="20" />&nbsp;已完成项目</MenuItem>
      <MenuItem name="4-2"><Icon type="ios-stats-outline" size="20" />&nbsp;任务统计</MenuItem>
      <MenuItem name="4-3"><Icon type="ios-settings-outline" size="20" />&nbsp;推送设置</MenuItem>
    </Submenu>
  </div>
</template>
<script>
export default {
  data () {
    return {
      menuSelectEventName: 'EfficientHeader-select',
      showProject: false,
      showIteration: false,
      projectCategorys: this.$const.constants.projectCategorys,
      newProjectForm: {
        category: '',
        type: 0,
        parentId: null,
        name: '',
        description: ''
      },
      newProjectValidate: {
        category: [{ required: true, message: '项目类型不能为空', trigger: 'blur' }],
        name: [{ required: true, message: '项目名称不能为空', trigger: 'blur' }],
        description: [
          { required: true, message: '项目描述不能为空', trigger: 'blur' },
          { type: 'string', max: 200, message: '描述最大长度不得超过200个字', trigger: 'blur' }
        ]
      },
      newIterationForm: {
        type: 1,
        parentId: '',
        name: '',
        description: ''
      },
      newIterationValidate: {
        parentId: [{ required: true, type: 'number', message: '所属项目不能为空', trigger: 'blur' }],
        name: [{ required: true, message: '日常名称不能为空', trigger: 'blur' }],
        description: [
          { required: true, message: '日常描述不能为空', trigger: 'blur' },
          { type: 'string', max: 200, message: '描述最大长度不得超过200个字', trigger: 'blur' }
        ]
      },
      allProject: []
    }
  },
  methods: {
    menuSelect (name) {
      switch (name) {
        case '2':
          this.$router.push('/efficient')
          break
        case '3-1':
          this.addProject()
          break
        case '3-2':
          this.addIteration()
          break
        case '4-1':
          this.$router.push('/efficient/completed')
          break
        case '4-2':
          this.$router.push('/efficient/statistics')
          break
        case '4-3':
          this.$router.push({
            path: '/efficient/setting',
            query: {
              tab: 1
            }
          })
          break
        default:
          break
      }
    },
    createProject (projectForm, successCallBack) {
      this.$Loading.start()
      this.$http.post(this.$const.efficient.api.project_create, projectForm).then(res => {
        if (res.code !== 0) {
          this.$Loading.error()
          return
        }
        window.eventBus.$emit('addProject')
        this.$Loading.finish()
        this.$Message.success('创建成功!')
        successCallBack()
      })
    },
    addProject () {
      this.showProject = true
    },
    projectOk () {
      this.$refs.newProjectForm.validate((valid) => {
        if (!valid) {
          return
        }
        this.createProject(this.newProjectForm, () => {
          this.$refs.newProjectForm.resetFields()
          this.showProject = false
        })
      })
    },
    projectCancel () {
      this.$refs.newProjectForm.resetFields()
    },
    addIteration () {
      this.$Loading.start()
      this.$http.post(this.$const.efficient.api.project_all, {}).then(res => {
        if (res.code !== 0) {
          this.$Loading.error()
          return
        }
        this.$Loading.finish()
        this.allProject = res.data
        this.showIteration = true
      })
    },
    iterationOk () {
      this.$refs.newIterationForm.validate((valid) => {
        if (!valid) {
          return
        }
        this.createProject(this.newIterationForm, () => {
          this.$refs.newIterationForm.resetFields()
          this.showIteration = false
        })
      })
    },
    iterationCancel () {
      this.$refs.newIterationForm.resetFields()
    }
  },
  created () {
    document.title = this.$const.moduleTitles.efficient
    window.eventBus.$on(this.menuSelectEventName, this.menuSelect)
  },
  destroyed () {
    window.eventBus.$off(this.menuSelectEventName, this.menuSelect)
  }
}
</script>



