<style scoped>
</style>
<template>
  <div class="admin-config">
    <Layout :style="{minHeight: containerHeight}">
      <Sider hide-trigger :style="{background: '#fff'}">
        <Menu active-name="1" theme="light" mode="vertical" width="auto" @on-select="select">
          <MenuItem name="1">
            <Icon type="ios-refresh" size="20" />
            动态配置
          </MenuItem>
        </Menu>
      </Sider>
      <Layout :style="{
        padding: '10px',
        maxHeight: contentHeight,
        overflow: 'auto'}">
        <Content :style="{
          padding: '10px',
          background: '#fff'}">
          <router-view/>
        </Content>
      </Layout>
    </Layout>
  </div>
</template>
<script>
export default {
  data () {
    return {
      containerHeight: '500px',
      contentHeight: '480px'
    }
  },
  methods: {
    select (name) {
      console.info('select name=' + name)
    }
  },
  created () {
    let containerHeight = window.localStorage.getItem('containerHeight')
    this.containerHeight = containerHeight * 1 + 32 + 'px'
    this.contentHeight = containerHeight * 1 + 32 - 20 + 'px'
  },
  destroyed () {}
}
</script>



