<style scoped>
.page-user-login {
  padding-top: 100px;
  background-color: #f1f1f1;
  text-align: center;
}
.box {
  width: 400px;
  margin: 60px auto 0;
  padding: 50px 50px 30px;
  background-color: #fff;
  border-radius: 4px;
  box-shadow: 0 0 8px rgba(0,0,0,.1);
  vertical-align: middle;
  display: inline-block;
}
.title {
  margin: 0 auto 35px;
  padding: 10px;
  font-weight: 400;
  color: #969696;
}
.title a {
  padding: 10px;
}
.title b {
  padding: 10px;
}
.active {
  font-weight: 700;
  border-bottom: 2px solid #2d8cf0;
}
h4 {
  font-size: 18px;
}
.sign-in-button {
  margin-top: 20px;
  width: 100%;
  padding: 9px 18px;
  font-size: 18px;
  border: none;
  border-radius: 25px;
  color: #fff;
  cursor: pointer;
  outline: none;
  display: block;
  clear: both;
}
.form-center {
  margin-left:-25px;
}
</style>
<template>
  <div class="page-user-login" :style="{height: allHeight}">
    <div class="box">
      <h4 class="title">
        <div class="normal-title">
          <a @click="handleLogin()">登录</a>
          <b>·</b>
          <a class="active" href="javascript:;">注册</a>
        </div>
      </h4>
      <Row>
        <Form ref="account" :model="account" :rules="ruleInline" :label-width="50">
          <FormItem prop="email" label="">
            <Input type="email" v-model="account.email" placeholder="邮箱" class="form-center">
              <Icon type="ios-mail-outline" slot="prepend"></Icon>
            </Input>
          </FormItem>
          <FormItem prop="nickname" label="">
            <Input type="text" v-model="account.nickname" placeholder="你的昵称" class="form-center">
              <Icon type="ios-person-outline" slot="prepend"></Icon>
            </Input>
          </FormItem>
          <FormItem prop="password" label="">
            <Input type="password" v-model="account.password" placeholder="设置密码" class="form-center">
              <Icon type="ios-lock-outline" slot="prepend"></Icon>
            </Input>
          </FormItem>
          <FormItem prop="checkpwd" label="">
            <Input type="password" v-model="account.checkpwd" placeholder="再次确认密码" class="form-center">
              <Icon type="ios-lock-outline" slot="prepend"></Icon>
            </Input>
          </FormItem>
          <FormItem>
            <Button class="sign-in-button form-center" type="primary" @click="handleRegister">注册</Button>
            <!-- <Button style="margin-left: 8px" @click="handleLogin()">已有账号</Button> -->
          </FormItem>
        </Form>
      </Row>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      allHeight: `${window.innerHeight}px`,
      account: {
        email: '',
        nickname: '',
        password: '',
        checkpwd: ''
      },
      ruleInline: {
        email: [
          { required: true, message: '登录邮箱不能为空', trigger: 'blur' },
          { type: 'email', min: 6, message: '邮箱格式不对', trigger: 'blur' }
        ],
        nickname: [
          { required: true, message: '昵称不能为空', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '注册密码不能为空', trigger: 'blur' },
          { type: 'string', min: 4, message: '密码长度不少于4位数', trigger: 'blur' }
        ],
        checkpwd: [
          { required: true, message: '验证密码不能为空', trigger: 'blur' },
          { type: 'string', min: 4, message: '密码长度不少于4位数', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    handleRegister () {
      this.$refs.account.validate((valid) => {
        if (!valid) {
          this.$Message.error('注册参数校验失败')
          return
        }
        if (this.password !== this.checkpwd) {
          this.$Message.error('密码与确认密码不相等')
          return
        }
        this.$Loading.start()
        this.$http.post(this.$const.uic.api.user_register_email, {
          email: this.account.email,
          nickname: this.account.nickname,
          password: this.$md5(this.account.password)
        }).then(res => {
          if (res.code !== 0) {
            this.$Loading.error()
            return
          }
          window.localStorage.setItem(this.$const.uic.local_token, res.data)
          this.$Loading.finish()
          this.$Message.success('注册成功！')
          if (this.$route.query.redirectUrl) {
            window.location = this.$route.query.redirectUrl
            return
          }
          this.$router.push('/')
        })
      })
    },
    handleLogin () {
      if (this.$route.query.redirectUrl) {
        this.$router.push(`/user/login?redirectUrl=${this.$route.query.redirectUrl}`)
        return
      }
      this.$router.push('/user/login')
    }
  },
  created () {
    document.title = `注册-${this.$const.moduleTitles.webset}`
  }
}
</script>



