<style scoped>
</style>
<template>
  <div class="admin-efficient-project">
    <Breadcrumb :style="{marginBottom: '10px'}">
        <BreadcrumbItem>效率平台</BreadcrumbItem>
        <BreadcrumbItem>项目管理</BreadcrumbItem>
    </Breadcrumb>
    <Row :style="{marginBottom: '10px'}">
      <Col span="3" style="padding-right:10px;">
        <Select v-model="filter.category" style="width:150px" placeholder="项目类型">
          <Option v-for="item in projectCategorys" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
      </Col>
      <Col span="3" style="padding-right:10px;">
        <Select v-model="filter.type" style="width:150px" placeholder="项目类别">
          <Option v-for="item in projectTypes" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
      </Col>
      <Col span="3" style="padding-right:10px;">
        <Select v-model="filter.status" style="width:150px" placeholder="状态">
          <Option v-for="item in statusList" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
      </Col>
      <Col span="3">
        <Input v-model="filter.name" placeholder="项目名" />
      </Col>
      <Col span="12">
        &nbsp;&nbsp;&nbsp;&nbsp;<Button type="primary" @click="search">查询</Button>&nbsp;&nbsp;&nbsp;&nbsp;
      </Col>
    </Row>
    <Table border :columns="tableColumns" :data="tableData"/>
    <Page
      :total="tableTotal"
      show-total
      show-sizer
      :page-size="tablePageSize"
      :page-size-opts="[10, 20, 30, 40]"
      @on-change="goPage"
      @on-page-size-change="pageSizeChange"
      :style="{marginTop: '10px', textAlign: 'right'}" />
  </div>
</template>
<script>
export default {
  data () {
    return {
      tableColumns: [{
        title: '用户昵称',
        key: 'userNickname',
        fixed: 'left',
        width: 100
      }, {
        title: '项目名',
        key: 'name',
        fixed: 'left',
        width: 100
      }, {
        title: '描述',
        key: 'description',
        width: 300
      }, {
        title: '项目类别',
        key: 'categoryLabel',
        width: 100
      }, {
        title: '项目类型',
        key: 'typeLabel',
        width: 180
      }, {
        title: '所属父项目名',
        key: 'parentName',
        width: 110
      }, {
        title: '状态',
        key: 'status',
        width: 80,
        render: (h, params) => {
          return h('div', params.row.status === 0 ? '未完成' : '已完成')
        }
      }, {
        title: '完成时间',
        key: 'endAt',
        width: 150
      }, {
        title: '创建时间',
        key: 'createAt',
        width: 150
      }, {
        title: '更新时间',
        key: 'updateAt',
        width: 150
      }],
      tableData: [],
      tableTotal: 0,
      tablePageSize: 10,
      tablePageNo: 1,
      filter: {
        category: '-1',
        type: -1,
        status: -1,
        name: ''
      },
      projectTypes: [{
        value: -1,
        label: '全部'
      }, ...this.$const.constants.projectTypes],
      projectCategorys: [{
        value: '-1',
        label: '全部'
      }, ...this.$const.constants.projectCategorys],
      statusList: [{
        value: -1,
        label: '全部'
      }, {
        value: 0,
        label: '未完成'
      }, {
        value: 1,
        label: '已完成'
      }]
    }
  },
  methods: {
    loadData () {
      this.$http.post(this.$const.efficient.api.project_admin_page, {
        pageSize: this.tablePageSize,
        pageNo: this.tablePageNo,
        filter: this.filter
      }).then(res => {
        if (res.code !== 0) {
          return
        }
        this.tableData = res.data.items
        this.tableTotal = res.data.totalNumber
      })
    },
    search () {
      this.tablePageNo = 1
      this.loadData()
    },
    goPage (pageNo) {
      this.tablePageNo = pageNo
      this.loadData()
    },
    pageSizeChange (pageSize) {
      this.tablePageSize = pageSize
      this.tablePageNo = 1
      this.loadData()
    }
  },
  created () {
    this.tablePageNo = 1
    this.loadData()
  }
}
</script>



