<style scoped>
* {
  box-sizing: border-box;
}
.efficient-home {
  height: 100%;
}
.project {
  margin-bottom: 20px;
  padding-right: 20px;
}
.project-task-box {
  min-height: 135px;
  max-height: 135px;
  overflow: auto;
}
.complete-checkbox {
  float: right;
}
.complete-box {
  box-sizing: border-box;
  background:#eee;
  padding: 10px 10px 0 10px;
}
.complete-item {
  box-sizing: border-box;
  margin-bottom: 10px;
}
</style>
<template>
  <div class="efficient-home">
    <Modal
      :loading="true"
      title="新建任务"
      v-model="showAddTask"
      :mask-closable="false"
      @on-ok="addTaskOk"
      @on-cancel="addTaskCancel">
      <Form ref="newTaskForm" :model="newTaskForm" :rules="newTaskValidate" :label-width="80">
        <FormItem label="所属项目">
          <p>{{newTaskForm.projectName}}</p>
        </FormItem>
        <FormItem label="任务名称" prop="name">
          <Input v-model="newTaskForm.name" placeholder="请输入任务名称..."/>
        </FormItem>
        <FormItem label="任务描述" prop="description">
          <Input v-model="newTaskForm.description" type="textarea" :autosize="{minRows: 5,maxRows: 15}" placeholder="请输入任务描述..."/>
        </FormItem>
        <FormItem label="定时提醒" prop="remind">
          <i-switch v-model="newTaskForm.remind" size="large">
            <span slot="open">是</span>
            <span slot="close">否</span>
          </i-switch>
        </FormItem>
        <FormItem v-if="newTaskForm.remind" label="定时提醒" prop="remind">
          <Row>
            <Col span="12">
              <DatePicker v-model="newTaskForm.dateStr" type="date" placeholder="请选择提醒日期" @on-change="dateChange"></DatePicker>
            </Col>
            <Col span="12">
              <TimePicker v-model="newTaskForm.timeStr" type="time" placeholder="请选择提醒时间点" @on-change="timeChange"></TimePicker>
            </Col>
          </Row>
        </FormItem>
      </Form>
    </Modal>
    <Row :style="{
        minHeight: containerHeight,
        maxHeight: containerHeight,
        overflow: 'auto'}">
        <Col span="18">
          <Breadcrumb style="margin-bottom: 10px;">
            <BreadcrumbItem>进行中项目</BreadcrumbItem>
          </Breadcrumb>
          <Row v-if="!projects || projects.length === 0"><h2 style="text-align: center;margin-top:100px;">暂无项目，点击右上角 + 新建一个吧 ~~</h2></Row>
          <Row v-if="projects && projects.length > 0" :style="{minHeight: contentHeight, maxHeight: contentHeight, overflow: 'auto'}">
            <Col v-for="project in projects" :key="project.id" span="8" class="project">
              <Card>
                <p slot="title">
                  <a href="#" slot="extra" @click.prevent="projectInfo(project.id)">
                    <Tooltip :content="`[${project.type}] ${project.description}`" placement="right-end">{{project.name}}</Tooltip>
                  </a>
                </p>
                <a href="#" slot="extra" @click.prevent="addTask(project.id, project.name)" title="添加任务"><Icon type="md-add"/></a>
                <div class="project-task-box">
                  <h5 v-if="!project.tasks || project.tasks.length === 0" style="text-align: center;;color:#c5c8ce;">暂无任务，点击右上角 + 新建一个吧 ~~</h5>
                  <p v-for="task in project.tasks" :key="task.id">
                    <Tooltip
                      :content="task.description"
                      placement="right-end"
                      style="width:275px;white-space: nowrap;overflow: hidden;text-overflow: ellipsis;">{{task.name}}</Tooltip>
                    <Checkbox class="complete-checkbox" @click.native="done(task.id)" />
                  </p>
                </div>
              </Card>
            </Col>
          </Row>
        </Col>
        <Col span="6">
          <div>
            <Breadcrumb style="margin-bottom: 10px;">
              <BreadcrumbItem>本周已完成</BreadcrumbItem>
            </Breadcrumb>
          </div>
          <div :style="{minHeight: contentHeight, maxHeight: contentHeight, overflow: 'auto'}" class="complete-box">
            <h3 v-if="!doneTasks || doneTasks.length === 0" style="text-align: center;margin-top:100px;">本周无已完成任务</h3>
            <Card v-for="task in doneTasks" :key="task.id" :bordered="false" class="complete-item">
              <p slot="title"><Tooltip :content="task.projectDescription" placement="right-end">{{task.projectName}}</Tooltip></p>
              <a href="#" slot="extra" @click.prevent="undone(task.id)" title="更新为未完成">
                <Checkbox v-model="completeTaskCheckV" />
              </a>
              <p><Tooltip :content="task.description" placement="top-end">{{task.name}}</Tooltip></p>
            </Card>
          </div>
        </Col>
      </Row>
  </div>
</template>
<script>
export default {
  data () {
    return {
      showAddTask: false,
      completeTaskCheckV: true,
      containerHeight: '500px',
      contentHeight: '450px',
      newTaskForm: {
        projectId: '',
        projectName: '',
        name: '',
        description: '',
        remind: false,
        remindTime: '',
        dateStr: '',
        timeStr: ''
      },
      newTaskValidate: {
        name: [{ required: true, message: '任务名称不能为空', trigger: 'blur' }],
        description: [
          { required: true, message: '任务描述不能为空', trigger: 'blur' },
          { type: 'string', max: 200, message: '描述最大长度不得超过200个字', trigger: 'blur' }
        ]
      },
      projects: [],
      doneTasks: []
    }
  },
  methods: {
    reloadProjects () {
      this.$Loading.start()
      this.$http.post(this.$const.efficient.api.project_page, {
        pageSize: 100,
        pageNo: 1,
        filter: { status: 0 }
      }).then(res => {
        if (res.code !== 0) {
          this.$Loading.error()
          return
        }
        this.$Loading.finish()
        this.projects = res.data.items
      })
    },
    reloadTasks () {
      this.$Loading.start()
      this.$http.post(this.$const.efficient.api.task_week_complete, {}).then(res => {
        if (res.code !== 0) {
          this.$Loading.error()
          return
        }
        this.doneTasks = res.data
        this.$Loading.finish()
      })
    },
    projectInfo (id) {
      this.$router.push(`/efficient/project/${id}`)
    },
    addTask (projectId, projectName) {
      this.newTaskForm.projectId = projectId
      this.newTaskForm.projectName = projectName
      this.newTaskForm.remind = false
      this.newTaskForm.remindTime = ''
      this.newTaskForm.dateStr = ''
      this.newTaskForm.timeStr = ''
      this.showAddTask = true
    },
    dateChange (dateStr) {
      if (new Date().getTime() > new Date(`${dateStr} 23:59:59`).getTime()) {
        this.$Message.error('日期不能小于当前时间!')
        this.newTaskForm.dateStr = ''
        return
      }
      this.newTaskForm.dateStr = dateStr
    },
    timeChange (timeStr) {
      if (!this.newTaskForm.dateStr) {
        this.$Message.error('请先选择日期!')
        return
      }
      if (new Date().getTime() > new Date(`${this.newTaskForm.dateStr} ${timeStr}`).getTime()) {
        this.$Message.error('时间不能小于当前时间!')
        this.newTaskForm.timeStr = ''
        return
      }
      this.newTaskForm.timeStr = timeStr
    },
    addTaskOk () {
      this.$refs.newTaskForm.validate((valid) => {
        if (!valid) {
          return
        }
        if (this.newTaskForm.remind) {
          if (!this.newTaskForm.dateStr || !this.newTaskForm.timeStr) {
            this.$Message.success('定时提醒任务提醒时间不可为空!')
            return
          }
          this.newTaskForm.remindTime = new Date(`${this.newTaskForm.dateStr} ${this.newTaskForm.timeStr}`).getTime()
        }
        this.$Loading.start()
        this.$http.post(this.$const.efficient.api.task_create, this.newTaskForm).then(res => {
          if (res.code !== 0) {
            this.$Loading.error()
            return
          }
          this.$refs.newTaskForm.resetFields()
          this.showAddTask = false
          this.$Loading.finish()
          this.$Message.success('任务创建成功!')
          this.reloadProjects()
        })
      })
    },
    addTaskCancel () {
      this.$refs.newTaskForm.resetFields()
    },
    done (id) {
      this.$http.post(this.$const.efficient.api.task_done + id, {}).then(res => {
        if (res.code !== 0) {
          return
        }
        this.$Message.success('好棒，继续加油鸭!')
        this.reloadProjects()
        this.reloadTasks()
      })
    },
    undone (id) {
      this.$http.post(this.$const.efficient.api.task_undone + id, {}).then(res => {
        if (res.code !== 0) {
          return
        }
        this.$Message.success('更新成功')
        this.reloadProjects()
        this.reloadTasks()
      })
    }
  },
  created () {
    window.eventBus.$on('addProject', this.reloadProjects)
    let containerHeight = window.localStorage.getItem('containerHeight')
    this.containerHeight = containerHeight + 'px'
    this.contentHeight = containerHeight - 32 + 'px'
    this.reloadProjects()
    this.reloadTasks()
  },
  destroyed () {
    window.eventBus.$off('addProject', this.reloadProjects)
  }
}
</script>



