<style scoped>
.layout{
  position: relative;
  overflow: hidden;
}
.ivu-drawer-body {
  padding: 0 !important;
}
.layout-logo{
  width: 30px;
  height: 30px;
  border-radius: 3px;
  float: left;
  position: relative;
  top: 15px;
  left: 20px;
  cursor: pointer;
}
.layout-nav{
  width: 1020px;
  margin: 0 auto;
  margin-right: 0px;
  float: right;
}
.haeder-menu {
  float: right !important;
}
.more-title {
  font-size: 18px;
  font-weight: bold;
  padding-bottom: 10px;
  cursor: pointer;
}
.qrcode-img {
  width: 258px;
  height: 258px;
}
.btn-proposal {
  width: 40px;
  height: 40px;
  position: fixed;
  right: 30px;
  bottom: 30px;
  background-color: #fff;
  border: 1px solid #EEEEEE;
  border-radius: 50%;
  box-shadow: 0 0 5px rgba(0,0,0,.05);
  cursor: pointer;
  text-align: center;
  color: #2d8cf0;
  font-size: 20px;
}
.layout-footer-center{
  text-align: center;
  height: 48px;
  padding: 10px !important;
}
</style>
<template>
    <div class="layout">
        <Drawer title="开发者客栈 >> 发现更多" placement="left" :closable="false" v-model="showHomeMenu">
          <p @click="goEfficient" class="more-title">效率平台</p>
          <p @click="showZyqs = true" class="more-title">智猿其说</p>
          <p @click="showBwj = true" class="more-title">帮我记</p>
          <p @click="showStudio = true" class="more-title">画板</p>
          <p v-if="role > 0" @click="goAdmin" class="more-title">管理后台</p>
          <!-- <h1><router-link to="/efficient">效率</router-link></h1> -->
          <!-- <h1><router-link to="/blog">博客</router-link></h1> -->
        </Drawer>
        <Drawer title="我的 / 设置" v-model="myShowSetting" width="720" :mask-closable="false" :styles="myDrawerStyles">
            <!-- <Form :model="formData">
                <FormItem label="Description" label-position="top">
                    <Input type="textarea" v-model="formData.desc" :rows="4" placeholder="please enter the description" />
                </FormItem>
            </Form> -->
            <div class="demo-drawer-footer">
                <Button style="margin-right: 8px" @click="myShowSetting = false">关闭</Button>
                <Button type="primary" @click="myShowSetting = false">保存</Button>
            </div>
        </Drawer>
        <!-- 微信小程序 -->
        <Modal
          v-model="showBwj"
          :mask-closable="false"
          title="帮我记-微信小程序">
          <p style="text-align:center;">
            <img :src="bwjQrcode" class="qrcode-img"/>
          </p>
          <p style="text-align:center;margin-top:30px;">一款随手记、计时做、有提醒、拒拖延的生活类小程序。</p>
        </Modal>
        <!-- 微信公众号 -->
        <Modal
          v-model="showZyqs"
          :mask-closable="false"
          title="智猿其说-微信公众号">
          <p style="text-align:center;">
            <img :src="zyqsQrcode" class="qrcode-img"/>
          </p>
          <p style="text-align:center;margin-top:30px;">来自一名程序猿的唠叨。</p>
        </Modal>
        <!-- 画室 -->
        <Modal
          title="画板"
          v-model="showStudio"
          @on-ok="goStudio">
          <Input v-model="studioName" placeholder="画板名称"/>
        </Modal>
        <!-- 建议反馈 -->
        <Modal
          title="建议反馈"
          ok-text="提交"
          cancel-text="关闭"
          v-model="showProposal"
          @on-ok="addProposal">
          <p>hello，感谢您来到{{websetTitle}}，您遇到任何问题都可以在这里留言，我们会尽快帮您解决。</p><br/>
          <Input v-model="proposal.content" placeholder="这里填写您的建议或意见......" type="textarea" :autosize="{minRows: 10,maxRows: 15}"/>
        </Modal>
        <Layout>
            <Header data-name="header" style="background: white;height: 60px;border-bottom: 1px solid #dcdee2;">
                <Menu mode="horizontal" theme="light" active-name="2" @on-select="select">
                    <div @click="showHomeMenu = true" class="layout-logo" :style="{background: 'url(' + logo + ')', backgroundSize: 'cover'}"></div>
                    <div class="layout-nav">
                        <Submenu v-if="user" name="1" class="haeder-menu">
                            <template slot="title">
                                <Avatar v-bind:src="user.avatarUrl">{{user.nickname}}</Avatar>
                                {{user.nickname}}
                            </template>
                            <MenuGroup title="我的">
                                <MenuItem name="1-1">
                                    <Icon type="ios-settings-outline" size="20" />&nbsp;设置
                                </MenuItem>
                                <!-- <MenuItem name="1-2">
                                    <Badge dot>
                                        <Icon type="ios-notifications-outline" size="20" />&nbsp;&nbsp;消息&nbsp;&nbsp;
                                    </Badge>
                                </MenuItem> -->
                            </MenuGroup>
                            <MenuGroup title="操作">
                                <MenuItem name="1-4">
                                    <Icon type="ios-log-out" size="20" />&nbsp;退出登录
                                </MenuItem>
                            </MenuGroup>
                        </Submenu>
                        <MenuItem
                            v-if="!user"
                            name="1"
                            class="haeder-menu"
                            @click.native="login">
                            <Icon type="ios-contact-outline" />
                            登录
                        </MenuItem>
                        <EfficientHeader v-if="headerTemplate === 'EfficientHeader'"/>
                        <BlogHeader v-if="headerTemplate === 'BlogHeader'"/>
                        <AdminHeader v-if="headerTemplate === 'AdminHeader'"/>
                    </div>
                </Menu>
            </Header>
            <Content v-if="paddingContent === 'false'" :style="{minHeight: contentHeight, maxHeight: contentHeight}">
                <slot></slot>
            </Content>
            <Content v-if="paddingContent !== 'false'" :style="{padding: contentPadding + 'px', minWidth: '1200px'}">
                <Card :style="{minHeight: contentHeight, maxHeight: contentHeight}">
                    <slot></slot>
                </Card>
            </Content>
            <div class="btn-proposal" title="建议反馈" @click="showProposal = true">
              <Icon type="ios-chatbubbles" />
            </div>
            <Footer v-if="showFooter !== 'false'" class="layout-footer-center">2016-{{new Date().getFullYear()}} &copy; developers 皖ICP备17014120号</Footer>
        </Layout>
    </div>
</template>
<script>
export default {
  props: ['fixedHeader', 'headerTemplate', 'paddingContent', 'showFooter'],
  data () {
    return {
      websetTitle: this.$const.websetTitle,
      showHomeMenu: false,
      showBwj: false,
      showZyqs: false,
      showStudio: false,
      showProposal: false,
      logo: require('../../assets/logo.jpeg'),
      bangwoji: require('../../assets/bangwoji.jpeg'),
      bwjQrcode: require('../../assets/bwj_qrcode.jpeg'),
      zyqsQrcode: require('../../assets/zyqs_qrcode.jpg'),
      user: null,
      role: 0,
      myShowSetting: false,
      studioName: '',
      proposal: {
        content: '',
        source: window.location.href
      },
      myDrawerStyles: {
        height: 'calc(100% - 55px)',
        overflow: 'auto',
        paddingBottom: '53px',
        position: 'static'
      },
      contentPadding: 10,
      contentHeight: '500px'
    }
  },
  created () {
    Promise.all([this.$http.get(this.$const.uic.api.user_info), this.$http.get(this.$const.uic.api.user_role)]).then(ress => {
      const userInfo = ress[0]
      const userRole = ress[1]
      if (!userInfo.data) {
        return
      }
      window.localStorage.setItem(this.$const.uic.login_user, JSON.stringify(userInfo.data))
      this.user = userInfo.data
      if (!userRole.data) {
        return
      }
      this.role = userRole.data
    })
    let allHeight = window.innerHeight
    let headerHeight = 60
    let footerHeight = 48
    let contentHeight = (allHeight - headerHeight - footerHeight - this.contentPadding * 2)
    if (this.showFooter === 'false') {
      contentHeight += footerHeight
    }
    if (this.paddingContent === 'false') {
      contentHeight += this.contentPadding * 2
    }
    window.localStorage.setItem('containerHeight', contentHeight - 32)
    this.contentHeight = contentHeight + 'px'
  },
  methods: {
    login () {
      this.$router.push('/user/login')
    },
    select (name) {
      if (name.substr(0, 1) === '1') {
        // console.info('个人中心 name=', name)
        switch (name) {
          case '1-1':
            this.myShowSetting = true
            break
          case '1-4':
            this.logout()
            break
          default:
            return
        }
      }
      window.eventBus.$emit(this.headerTemplate + '-select', name)
    },
    logout () {
      this.$http.post(this.$const.uic.api.user_logout, {}).then(res => {
        if (res.code === 0) {
          window.localStorage.removeItem(this.$const.uic.local_token)
          this.$router.push('/user/login')
        }
      })
    },
    goStudio () {
      window.open('http://www.qbian.me:8082?id=' + this.studioName)
    },
    addProposal () {
      this.proposal.source = window.location.href
      this.$Loading.start()
      this.$http.post(this.$const.system.api.proposal_add, this.proposal).then(res => {
        if (res.code !== 0) {
          this.$Loading.error()
          return
        }
        this.$Loading.finish()
        this.proposal.content = ''
        this.$Message.success('建议反馈成功!')
      })
    },
    goEfficient () {
      this.$router.push('/efficient')
    },
    goAdmin () {
      this.$router.push('/admin')
    }
  }
}
</script>



