<style scoped>
.project {
  margin-bottom: 20px;
}
.project-task-box {
  min-height: 135px;
  max-height: 135px;
  overflow: auto;
}
</style>
<template>
  <div class="efficient-complete">
    <Row :style="{
      minHeight: containerHeight,
      maxHeight: containerHeight,
      overflow: 'auto'}" :gutter="0">
      <Col span="24">
        <Breadcrumb style="margin-bottom: 10px;">
          <BreadcrumbItem>已完成项目</BreadcrumbItem>
        </Breadcrumb>
        <Row v-if="!projects || projects.length === 0"><h2 style="text-align: center;margin-top:100px;">暂无已完成项目</h2></Row>
        <Row v-if="projects && projects.length > 0" :style="{minHeight: contentHeight, maxHeight: contentHeight, overflow: 'auto'}">
          <Col v-for="project in projects" :key="project.id" span="6" class="project" style="padding:0 8px;">
            <Card>
              <p slot="title">
                <a href="#" slot="extra" @click.prevent="projectInfo(project.id)">
                  <Tooltip :content="`[${project.type}] [${project.endAt} 完成] ${project.description}`" placement="right-end">{{project.name}}</Tooltip>
                </a>
              </p>
              <div class="project-task-box">
                <h5 v-if="!project.tasks || project.tasks.length === 0" style="text-align: center;;color:#c5c8ce;">暂无任务</h5>
                <p v-for="task in project.tasks" :key="task.id">
                  <Tooltip
                    :content="task.description"
                    placement="right-end"
                    style="white-space: nowrap;overflow: hidden;text-overflow: ellipsis;">{{task.name}}</Tooltip>
                </p>
              </div>
            </Card>
          </Col>
        </Row>
      </Col>
    </Row>
  </div>
</template>
<script>
export default {
  data () {
    return {
      containerHeight: '500px',
      contentHeight: '450px',
      projects: []
    }
  },
  methods: {
    loadProjects () {
      this.$Loading.start()
      this.$http.post(this.$const.efficient.api.project_page, {
        pageSize: 100,
        pageNo: 1,
        filter: { status: 1 }
      }).then(res => {
        if (res.code !== 0) {
          this.$Loading.error()
          return
        }
        this.$Loading.finish()
        this.projects = res.data.items
      })
    },
    projectInfo (id) {
      this.$router.push(`/efficient/project/${id}`)
    }
  },
  created () {
    let containerHeight = window.localStorage.getItem('containerHeight')
    this.containerHeight = containerHeight + 'px'
    this.contentHeight = containerHeight - 32 + 'px'
    this.loadProjects()
  }
}
</script>



