<style scoped>
.title {
  margin-top: 250px;
  text-align: center;
}
</style>
<template>
  <div class="admin-home">
    <Content :style="{margin: '10px', padding: '10px', minHeight: containerHeight, background: '#fff'}">
      <h1 class="title">欢迎使用开发者社区管理后台</h1>
    </Content>
  </div>
</template>
<script>
export default {
  data () {
    return {
      containerHeight: '500px'
    }
  },
  methods: {},
  created () {
    let containerHeight = window.localStorage.getItem('containerHeight')
    this.containerHeight = containerHeight * 1 + 32 + 'px'
  },
  destroyed () {}
}
</script>



