<style scoped>
</style>
<template>
  <div class="admin-config-dynamic">
    <Modal
      :loading="true"
      title="新增配置"
      v-model="showCreateMode"
      :mask-closable="false"
      @on-ok="createOk"
      @on-cancel="createCancel">
      <Form ref="newForm" :model="newForm" :rules="newFormValidate" :label-width="80">
        <FormItem label="键" prop="key">
          <Input v-model="newForm.key" placeholder="请输入配置键..."/>
        </FormItem>
        <FormItem label="值" prop="value">
          <Input v-model="newForm.value" placeholder="请输入配置值..."/>
        </FormItem>
        <FormItem label="描述" prop="remark">
          <Input v-model="newForm.remark" type="textarea" :autosize="{minRows: 5,maxRows: 15}" placeholder="请输入配置描述..."/>
        </FormItem>
      </Form>
    </Modal>
    <Modal
      :loading="true"
      title="更新配置"
      v-model="showUpdateMode"
      :mask-closable="false"
      @on-ok="updateOk">
      <Form ref="updateForm" :model="updateForm" :rules="updateFormValidate" :label-width="80">
        <FormItem label="键" prop="key">
          <Input v-model="updateForm.key" placeholder="请输入配置键..."/>
        </FormItem>
        <FormItem label="值" prop="value">
          <Input v-model="updateForm.value" placeholder="请输入配置值..."/>
        </FormItem>
        <FormItem label="描述" prop="remark">
          <Input v-model="updateForm.remark" type="textarea" :autosize="{minRows: 5,maxRows: 15}" placeholder="请输入配置描述..."/>
        </FormItem>
      </Form>
    </Modal>
    <Breadcrumb :style="{marginBottom: '10px'}">
        <BreadcrumbItem>配置中心</BreadcrumbItem>
        <BreadcrumbItem>动态配置</BreadcrumbItem>
    </Breadcrumb>
    <Row :style="{marginBottom: '10px'}">
      <Col span="4">
        <Input v-model="filter" placeholder="根据备注查询" />
      </Col>
      <Col span="16">
        &nbsp;&nbsp;&nbsp;&nbsp;<Button type="primary" @click="search">查询</Button>&nbsp;&nbsp;&nbsp;&nbsp;
      </Col>
      <Col span="4" :style="{textAlign: 'right'}">
        <Button type="success" @click="showCreateMode = true">新增</Button>
      </Col>
    </Row>
    <Table border :columns="tableColumns" :data="tableData">
      <template slot-scope="{ row, index }" slot="action">
        <Button type="primary" size="small" style="margin-right: 5px" @click="showUpdate(row)">更新</Button>
        <Poptip
          confirm
          placement="left"
          title="确定要删除吗？不可恢复！"
          @on-ok="deleteOk(row)">
          <Button type="error" size="small">删除</Button>
        </Poptip>
      </template>
    </Table>
    <Page
      :total="tableTotal"
      show-total
      show-sizer
      :page-size="tablePageSize"
      :page-size-opts="[10, 20, 30, 40]"
      @on-change="goPage"
      @on-page-size-change="pageSizeChange"
      :style="{marginTop: '10px', textAlign: 'right'}" />
  </div>
</template>
<script>
export default {
  data () {
    return {
      showCreateMode: false,
      showUpdateMode: false,
      tableColumns: [{
        title: '键',
        key: 'key'
      }, {
        title: '值',
        key: 'value'
      }, {
        title: '备注',
        key: 'remark'
      }, {
        title: '更新时间',
        key: 'updateAt'
      }, {
        title: '操作',
        slot: 'action',
        width: 150,
        align: 'center'
      }],
      tableData: [],
      tableTotal: 0,
      tablePageSize: 10,
      tablePageNo: 1,
      filter: null,
      newForm: {
        key: '',
        value: '',
        remark: ''
      },
      newFormValidate: {
        key: [
          { required: true, message: '配置键不能为空', trigger: 'blur' },
          { type: 'string', max: 200, message: '最大长度不得超过200个字', trigger: 'blur' }
        ],
        value: [
          { required: true, message: '配置值不能为空', trigger: 'blur' },
          { type: 'string', max: 8096, message: '最大长度不得超过8094个字', trigger: 'blur' }
        ],
        remark: [
          { required: true, message: '配置描述不能为空', trigger: 'blur' },
          { type: 'string', max: 200, message: '最大长度不得超过200个字', trigger: 'blur' }
        ]
      },
      updateForm: {
        key: '',
        value: '',
        remark: '',
        id: null
      },
      updateFormValidate: {
        key: [
          { required: true, message: '配置键不能为空', trigger: 'blur' },
          { type: 'string', max: 200, message: '最大长度不得超过200个字', trigger: 'blur' }
        ],
        value: [
          { required: true, message: '配置值不能为空', trigger: 'blur' },
          { type: 'string', max: 8096, message: '最大长度不得超过8094个字', trigger: 'blur' }
        ],
        remark: [
          { required: true, message: '配置描述不能为空', trigger: 'blur' },
          { type: 'string', max: 200, message: '最大长度不得超过200个字', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    loadData () {
      this.$http.post(this.$const.config.api.dynamic_page, {
        pageSize: this.tablePageSize,
        pageNo: this.tablePageNo,
        filter: this.filter
      }).then(res => {
        if (res.code !== 0) {
          return
        }
        this.tableData = res.data.items
        this.tableTotal = res.data.totalNumber
      })
    },
    search () {
      this.tablePageNo = 1
      this.loadData()
    },
    goPage (pageNo) {
      this.tablePageNo = pageNo
      this.loadData()
    },
    pageSizeChange (pageSize) {
      this.tablePageSize = pageSize
      this.tablePageNo = 1
      this.loadData()
    },
    showCreate () {
      this.showCreateMode = true
    },
    createOk () {
      this.$refs.newForm.validate((valid) => {
        if (!valid) {
          return
        }
        this.$Loading.start()
        this.$http.post(this.$const.config.api.dynamic_create, this.newForm).then(res => {
          if (res.code !== 0) {
            this.$Loading.error()
            return
          }
          this.$refs.newForm.resetFields()
          this.showCreateMode = false
          this.$Loading.finish()
          this.$Message.success('新增配置成功!')
          this.tablePageNo = 1
          this.loadData()
        })
      })
    },
    createCancel () {
      this.$refs.newForm.resetFields()
    },
    showUpdate (row) {
      this.updateForm = row
      this.showUpdateMode = true
    },
    updateOk () {
      this.$refs.updateForm.validate((valid) => {
        if (!valid) {
          return
        }
        this.$Loading.start()
        this.$http.post(this.$const.config.api.dynamic_update, this.updateForm).then(res => {
          if (res.code !== 0) {
            this.$Loading.error()
            return
          }
          this.$refs.updateForm.resetFields()
          this.showUpdateMode = false
          this.$Loading.finish()
          this.$Message.success('更新配置成功!')
          this.loadData()
        })
      })
    },
    deleteOk (row) {
      this.$Loading.start()
      this.$http.post(this.$const.config.api.dynamic_delete + row.id, {}).then(res => {
        if (res.code !== 0) {
          this.$Loading.error()
          return
        }
        this.$Loading.finish()
        this.$Message.success('删除配置成功!')
        this.loadData()
      })
    }
  },
  created () {
    this.tablePageNo = 1
    this.loadData()
  }
}
</script>



