<style scoped>
</style>
<template>
  <div class="admin-efficient">
    <Layout :style="{minHeight: containerHeight}">
      <Sider hide-trigger :style="{background: '#fff'}">
        <Menu active-name="1" theme="light" mode="vertical" width="auto" @on-select="select">
          <MenuItem name="1">
            <Icon type="ios-trophy-outline" size="20" />
            项目管理
          </MenuItem>
          <MenuItem name="2">
            <Icon type="ios-list-box-outline" size="20" />
            任务管理
          </MenuItem>
          <MenuItem name="3">
            <Icon type="ios-at-outline" size="20" />
            推送地址管理
          </MenuItem>
          <MenuItem name="4">
            <Icon type="ios-alarm-outline" size="20" />
            推送时间管理
          </MenuItem>
        </Menu>
      </Sider>
      <Layout :style="{
        padding: '10px',
        maxHeight: contentHeight,
        overflow: 'auto'}">
        <Content :style="{
          padding: '10px',
          background: '#fff'}">
          <router-view/>
        </Content>
      </Layout>
    </Layout>
  </div>
</template>
<script>
export default {
  data () {
    return {
      containerHeight: '500px',
      contentHeight: '480px'
    }
  },
  methods: {
    select (name) {
      switch (name) {
        case '1':
          this.$router.push('/admin/efficient/project')
          break
        case '2':
          this.$router.push('/admin/efficient/task')
          break
        case '3':
          this.$router.push('/admin/efficient/push-address')
          break
        case '4':
          this.$router.push('/admin/efficient/push-time')
          break
        default:
          this.$router.push('/admin/efficient/project')
          break
      }
    }
  },
  created () {
    let containerHeight = window.localStorage.getItem('containerHeight')
    this.containerHeight = containerHeight * 1 + 32 + 'px'
    this.contentHeight = containerHeight * 1 + 32 - 20 + 'px'
  },
  destroyed () {}
}
</script>



