<style scoped>
</style>
<template>
  <div class="haeder-menu">
    <MenuItem name="2">
      <Icon type="ios-home-outline" size="20" />
      首页
    </MenuItem>
    <MenuItem v-if="role > 1" name="3">
      <Icon type="ios-cloud-upload-outline" size="20" />
      配置中心
    </MenuItem>
    <MenuItem name="4">
      <Icon type="ios-contacts-outline" size="20" />
      用户中心
    </MenuItem>
    <MenuItem name="5">
      <Icon type="ios-chatbubbles-outline" size="20" />
      消息中心
    </MenuItem>
    <MenuItem name="6">
      <Icon type="ios-alarm-outline" size="20" />
      帮我记
    </MenuItem>
    <MenuItem name="7">
      <Icon type="ios-flag-outline" size="20" />
      效率平台
    </MenuItem>
  </div>
</template>
<script>
export default {
  data () {
    return {
      menuSelectEventName: 'AdminHeader-select',
      role: 1
    }
  },
  methods: {
    menuSelect (name) {
      switch (name) {
        case '2':
          this.$router.push('/admin')
          break
        case '3':
          this.$router.push('/admin/config')
          break
        case '4':
          this.$router.push('/admin/user')
          break
        case '5':
          this.$router.push('/admin/message')
          break
        case '6':
          this.$router.push('/admin/help-remember')
          break
        case '7':
          this.$router.push('/admin/efficient')
          break
        default:
          break
      }
    }
  },
  created () {
    document.title = this.$const.moduleTitles.admin
    window.eventBus.$on(this.menuSelectEventName, this.menuSelect)
    this.$http.get(this.$const.uic.api.user_role).then(res => {
      if (!res.data) {
        return
      }
      if (this.role < 1) {
        this.$router.push('/efficient')
        return
      }
      this.role = res.data
    })
  },
  destroyed () {
    window.eventBus.$off(this.menuSelectEventName, this.menuSelect)
  }
}
</script>



