<style scoped>
</style>
<template>
  <div class="admin-user-user-manager">
    <Modal
      :loading="true"
      title="更新配置"
      v-model="showUpdateMode"
      :mask-closable="false"
      @on-ok="updateOk">
      <Form ref="updateForm" :model="updateForm" :rules="updateFormValidate" :label-width="80">
        <FormItem label="键" prop="key">
          <Input v-model="updateForm.key" placeholder="请输入配置键..."/>
        </FormItem>
        <FormItem label="值" prop="value">
          <Input v-model="updateForm.value" placeholder="请输入配置值..."/>
        </FormItem>
        <FormItem label="描述" prop="remark">
          <Input v-model="updateForm.remark" type="textarea" :autosize="{minRows: 5,maxRows: 15}" placeholder="请输入配置描述..."/>
        </FormItem>
      </Form>
    </Modal>
    <Breadcrumb :style="{marginBottom: '10px'}">
        <BreadcrumbItem>用户中心</BreadcrumbItem>
        <BreadcrumbItem>用户管理</BreadcrumbItem>
    </Breadcrumb>
    <Row :style="{marginBottom: '10px'}">
      <Col span="4">
        <Input v-model="filter" placeholder="昵称" />
      </Col>
      <Col span="20">
        &nbsp;&nbsp;&nbsp;&nbsp;<Button type="primary" @click="search">查询</Button>&nbsp;&nbsp;&nbsp;&nbsp;
      </Col>
    </Row>
    <Table border :columns="tableColumns" :data="tableData">
      <template slot-scope="{ row, index }" slot="action">
        <!-- <Button type="primary" size="small" style="margin-right: 5px" @click="showUpdate(row)">更新</Button> -->
        <Poptip
          style="z-index:1000"
          confirm
          placement="left"
          title="确定要禁/启用吗？"
          @on-ok="disableOk(row)">
          <Button v-if="row.status === 0" type="success" size="small">启用</Button>
          <Button v-if="row.status !== 0" type="error" size="small">禁用</Button>
        </Poptip>
      </template>
    </Table>
    <Page
      :total="tableTotal"
      show-total
      show-sizer
      :page-size="tablePageSize"
      :page-size-opts="[10, 20, 30, 40]"
      @on-change="goPage"
      @on-page-size-change="pageSizeChange"
      :style="{marginTop: '10px', textAlign: 'right'}" />
  </div>
</template>
<script>
export default {
  data () {
    return {
      showUpdateMode: false,
      tableColumns: [{
        title: '头像',
        key: 'avatarUrl',
        width: 70,
        fixed: 'left',
        render: (h, params) => {
          return h('Avatar', {
            props: {
              src: params.row.avatarUrl
            }
          }, params.row.nickname)
        }
      }, {
        title: '昵称',
        key: 'nickname',
        fixed: 'left',
        width: 100
      }, {
        title: '角色',
        key: 'role',
        width: 100,
        render: (h, params) => {
          return h('div', params.row.role === 0 ? '用户' : (params.row.role === 1 ? '管理员' : '超级管理员'))
        }
      }, {
        title: '邮箱',
        key: 'email',
        width: 180
      }, {
        title: '手机号',
        key: 'phone',
        width: 180
      }, {
        title: '性别',
        key: 'gender',
        width: 100,
        render: (h, params) => {
          return h('div', params.row.gender === 0 ? '未知' : (params.row.gender === 1 ? '男' : '女'))
        }
      }, {
        title: '状态',
        key: 'status',
        width: 100,
        render: (h, params) => {
          return h('div', params.row.status === -1 ? '待激活' : (params.row.status === 0 ? '禁用' : '启用'))
        }
      }, {
        title: '描述',
        key: 'description',
        width: 100
      }, {
        title: '创建时间',
        key: 'createAt',
        width: 150
      }, {
        title: '更新时间',
        key: 'updateAt',
        width: 150
      }, {
        title: '操作',
        fixed: 'right',
        slot: 'action',
        width: 150,
        align: 'center'
      }],
      tableData: [],
      tableTotal: 0,
      tablePageSize: 10,
      tablePageNo: 1,
      filter: null,
      updateForm: {
        key: '',
        value: '',
        remark: '',
        id: null
      },
      updateFormValidate: {
        key: [
          { required: true, message: '配置键不能为空', trigger: 'blur' },
          { type: 'string', max: 200, message: '最大长度不得超过200个字', trigger: 'blur' }
        ],
        value: [
          { required: true, message: '配置值不能为空', trigger: 'blur' },
          { type: 'string', max: 8096, message: '最大长度不得超过8094个字', trigger: 'blur' }
        ],
        remark: [
          { required: true, message: '配置描述不能为空', trigger: 'blur' },
          { type: 'string', max: 200, message: '最大长度不得超过200个字', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    loadData () {
      this.$http.post(this.$const.uic.api.user_page, {
        pageSize: this.tablePageSize,
        pageNo: this.tablePageNo,
        filter: this.filter
      }).then(res => {
        if (res.code !== 0) {
          return
        }
        this.tableData = res.data.items
        this.tableTotal = res.data.totalNumber
      })
    },
    search () {
      this.tablePageNo = 1
      this.loadData()
    },
    goPage (pageNo) {
      this.tablePageNo = pageNo
      this.loadData()
    },
    pageSizeChange (pageSize) {
      this.tablePageSize = pageSize
      this.tablePageNo = 1
      this.loadData()
    },
    disableOk (row) {
      this.$Loading.start()
      this.$http.post(this.$const.uic.api.user_status + row.id, {}).then(res => {
        if (res.code !== 0) {
          this.$Loading.error()
          return
        }
        this.$Loading.finish()
        this.$Message.success('更新状态成功!')
        this.loadData()
      })
    },
    showUpdate (row) {
      this.updateForm = row
      this.showUpdateMode = true
    },
    updateOk () {
      this.$refs.updateForm.validate((valid) => {
        if (!valid) {
          return
        }
        this.$Loading.start()
        this.$http.post(this.$const.config.api.dynamic_update, this.updateForm).then(res => {
          if (res.code !== 0) {
            this.$Loading.error()
            return
          }
          this.$refs.updateForm.resetFields()
          this.showUpdateMode = false
          this.$Loading.finish()
          this.$Message.success('更新配置成功!')
          this.loadData()
        })
      })
    }
  },
  created () {
    this.tablePageNo = 1
    this.loadData()
  }
}
</script>



