// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import mavonEditor from 'mavon-editor'
import App from './App'
import router from './router.js'
import iView from 'iview'
import axios from './config/http'
import constant from './config/constant'
import MyLayout from './components/components/MyLayout.vue'
import EfficientHeader from './components/components/EfficientHeader.vue'
import EfficientIndex from './components/efficient/Index.vue'
import BlogHeader from './components/components/BlogHeader.vue'
import AdminHeader from './components/components/AdminHeader.vue'
import md5 from 'js-md5'
import 'iview/dist/styles/iview.css'
import 'mavon-editor/dist/css/index.css'
// import './assets/css/common.less'

Vue.config.productionTip = false
Vue.prototype.$http = axios
Vue.prototype.$const = constant
Vue.prototype.$md5 = md5
window.eventBus = new Vue() // 注册全局事件对象
Vue.use(mavonEditor)
Vue.use(iView)
Vue.component('MyLayout', MyLayout)
Vue.component('EfficientHeader', EfficientHeader)
Vue.component('EfficientIndex', EfficientIndex)
Vue.component('BlogHeader', BlogHeader)
Vue.component('AdminHeader', AdminHeader)

router.beforeEach((to, from, next) => {
  iView.LoadingBar.start()
  next()
})

router.afterEach(route => {
  iView.LoadingBar.finish()
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



