import Index from './Index.vue'
import Home from './Home.vue'
import Config from './Config.vue'
import ConfigDynamic from './config/Dynamic.vue'

import Efficient from './Efficient.vue'
import EfficientProject from './efficient/Project.vue'
import EfficientTask from './efficient/Task.vue'
import EfficientPushAddress from './efficient/PushAddress.vue'
import EfficientPushTime from './efficient/PushTime.vue'

import User from './User.vue'
import UserManager from './user/UserManager.vue'
import LoginHistory from './user/LoginHistory.vue'

import Message from './Message.vue'
import MessageMail from './message/Mail.vue'

import HelpRemember from './HelpRemember.vue'

export default [{
  path: '/admin',
  component: Index,
  children: [{
    path: '/',
    component: Home
  },
  // 配置中心
  {
    path: '/admin/config',
    component: Config,
    children: [{
      path: '/',
      component: ConfigDynamic
    }, {
      path: '/admin/config/dynamic',
      component: ConfigDynamic
    }]
  },
  // 效率平台
  {
    path: '/admin/efficient',
    component: Efficient,
    children: [{
      path: '/',
      component: EfficientProject
    }, {
      path: '/admin/efficient/project',
      component: EfficientProject
    }, {
      path: '/admin/efficient/task',
      component: EfficientTask
    }, {
      path: '/admin/efficient/push-address',
      component: EfficientPushAddress
    }, {
      path: '/admin/efficient/push-time',
      component: EfficientPushTime
    }]
  },
  // 用户中心
  {
    path: '/admin/user',
    component: User,
    children: [{
      path: '/',
      component: UserManager
    }, {
      path: '/admin/user/manager',
      component: UserManager
    }, {
      path: '/admin/user/login-history',
      component: LoginHistory
    }]
  },
  // 消息中心
  {
    path: '/admin/message',
    component: Message,
    children: [{
      path: '/',
      component: MessageMail
    }, {
      path: '/admin/message/mail',
      component: MessageMail
    }]
  },
  // 帮我记
  {
    path: '/admin/help-remember',
    component: HelpRemember
  }]
}]



