<style scoped>
html,body {
  width: 100%;
  height: 100%;
}
.container {
  padding-top: 100px;
}
.ivu-row {
  margin-bottom: 10px;
}
.ivu-col {
  /* border: 1px solid red; */
  height: 150px;
  text-align: center;
}
</style>
<template>
  <div class="container">
    <Row>
      <Col span="4" offset="10">
        <router-link to="/user/login">登录</router-link>
        <Badge :count="14">
          <Avatar
            v-bind:src="user.avatarUrl"
            size="large"/>
        </Badge>
      </Col>
    </Row>
    <Row>
      <Col span="4" offset="4">
        <router-link to="/efficient">效率</router-link>
      </Col>
      <!-- <Col span="4">
        <router-link to="/blog">博客</router-link>
      </Col>
      <Col span="4">
        <router-link to="/blog">博客</router-link>
      </Col>
      <Col span="4">
        <router-link to="/blog">博客</router-link>
      </Col> -->
    </Row>
  </div>
</template>

<script>
export default {
  data () {
    return {
      user: {
        avatarUrl: '',
        nickname: ''
      }
    }
  },
  beforeCreate () {
    this.$Loading.start()
    let token = window.localStorage.getItem(this.$const.uic.local_token)
    if (!token) {
      this.$Loading.finish()
      return
    }
    this.$http.get(this.$const.uic.api.user_info).then(res => {
      this.$Loading.finish()
      if (!res.data) {
        return
      }
      const user = res.data
      this.user.avatarUrl = user.avatarUrl
      this.user.nickname = user.nickname
      window.localStorage.setItem(this.$const.uic.login_user, JSON.stringify(user))
    })
  },
  // mounted: () => console.info('mounted'),
  created: () => {
    window.location.href = window.location.origin + '/efficient'
  }
  // destroyed: () => console.info('destroyed')
}
</script>



