const util = {
  newDate () {
    const date = new Date()
    return `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()} ${date.getHours()}:${date.getMinutes()}:${date.getMilliseconds()}`
  },
  beginDayTime () {
    const date = new Date()
    return new Date(`${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()} 00:00:00`).getTime()
  },
  nowDayTime (time) {
    const date = new Date()
    return new Date(`${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()} ${time}`).getTime()
  },
  getTimeStr (time) {
    const date = new Date(this.beginDayTime() + time)
    return `${date.getHours()}:${date.getMinutes()}:${date.getMilliseconds()}`
  }
}

