<style scoped>
</style>
<template>
  <div class="admin-message-mail">
    <Modal
      title="消息详情"
      v-model="showInfoMode"
      :mask-closable="false">
      <!-- 0:html、1:text -->
      <p v-if="infoForm.contentType === 0" v-html="infoForm.content"></p>
      <Input v-if="infoForm.contentType === 1" v-model="infoForm.content" type="textarea" :rows="15" />
    </Modal>
    <Breadcrumb :style="{marginBottom: '10px'}">
        <BreadcrumbItem>消息中心</BreadcrumbItem>
        <BreadcrumbItem>邮件消息</BreadcrumbItem>
    </Breadcrumb>
    <Row :style="{marginBottom: '10px'}">
      <Col span="3" style="padding-right:10px;">
        <Select v-model="filter.bizLine" style="width:150px" placeholder="业务线">
          <Option v-for="item in bizLines" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
      </Col>
      <Col span="3" style="padding-right:10px;">
        <Input v-model="filter.receiver" placeholder="收件邮箱" />
      </Col>
      <Col span="3" style="padding-right:10px;">
        <Input v-model="filter.title" placeholder="标题" />
      </Col>
      <Col span="3" style="padding-right:10px;">
        <Input v-model="filter.content" placeholder="内容" />
      </Col>
      <Col span="12">
        &nbsp;&nbsp;&nbsp;&nbsp;<Button type="primary" @click="search">查询</Button>&nbsp;&nbsp;&nbsp;&nbsp;
      </Col>
    </Row>
    <Table border :columns="tableColumns" :data="tableData">
      <template slot-scope="{ row, index }" slot="action">
        <Button type="primary" size="small" style="margin-right: 5px" @click="showInfo(row)">详情</Button>
      </template>
    </Table>
    <Page
      :total="tableTotal"
      show-total
      show-sizer
      :page-size="tablePageSize"
      :page-size-opts="[10, 20, 30, 40]"
      @on-change="goPage"
      @on-page-size-change="pageSizeChange"
      :style="{marginTop: '10px', textAlign: 'right'}" />
  </div>
</template>
<script>
export default {
  data () {
    return {
      showInfoMode: false,
      bizLines: [{value: -1, label: '全部'}, ...this.$const.constants.messageBizLine],
      tableColumns: [{
        title: '业务线',
        key: 'bizLineLabel',
        width: 90,
        fixed: 'left'
      }, {
        title: '收件邮箱',
        key: 'receiver',
        width: 160,
        fixed: 'left'
      }, {
        title: '发件邮箱',
        key: 'sender',
        width: 180
      }, {
        title: '标题',
        key: 'title',
        width: 200
      }, {
        title: '内容类型',
        key: 'contentTypeLabel',
        width: 100
      }, {
        title: '业务类型',
        key: 'bizType',
        width: 100
      }, {
        title: '内容',
        key: 'content',
        width: 300,
        render: (h, params) => {
          return h('div', params.row.content.length > 100 ? params.row.content.substring(0, 100) : params.row.content)
        }
      }, {
        title: '创建时间',
        key: 'createAt',
        width: 150
      }, {
        title: '更新时间',
        key: 'updateAt',
        width: 150
      }, {
        title: '操作',
        fixed: 'right',
        slot: 'action',
        width: 100,
        align: 'center'
      }],
      tableData: [],
      tableTotal: 0,
      tablePageSize: 10,
      tablePageNo: 1,
      filter: {
        bizLine: -1,
        receiver: '',
        title: '',
        content: ''
      },
      infoForm: {
        key: '',
        value: '',
        remark: '',
        id: null
      }
    }
  },
  methods: {
    loadData () {
      this.$http.post(this.$const.message.api.email_page, {
        pageSize: this.tablePageSize,
        pageNo: this.tablePageNo,
        filter: this.filter
      }).then(res => {
        if (res.code !== 0) {
          return
        }
        this.tableData = res.data.items
        this.tableTotal = res.data.totalNumber
      })
    },
    search () {
      this.tablePageNo = 1
      this.loadData()
    },
    goPage (pageNo) {
      this.tablePageNo = pageNo
      this.loadData()
    },
    pageSizeChange (pageSize) {
      this.tablePageSize = pageSize
      this.tablePageNo = 1
      this.loadData()
    },
    showInfo (row) {
      this.infoForm = row
      this.showInfoMode = true
    }
  },
  created () {
    this.tablePageNo = 1
    this.loadData()
  }
}
</script>



