<style scoped>
.page-user-login {
  padding-top: 100px;
  background-color: #f1f1f1;
  text-align: center;
}
.box {
  width: 400px;
  margin: 60px auto 0;
  padding: 50px 50px 30px;
  background-color: #fff;
  border-radius: 4px;
  box-shadow: 0 0 8px rgba(0,0,0,.1);
  vertical-align: middle;
  display: inline-block;
}
.title {
  margin: 0 auto 35px;
  padding: 10px;
  font-weight: 400;
  color: #969696;
}
.title a {
  padding: 10px;
}
.title b {
  padding: 10px;
}
.active {
  font-weight: 700;
  border-bottom: 2px solid #2d8cf0;
}
h4 {
  font-size: 18px;
}
.sign-in-button {
  margin-top: 20px;
  width: 100%;
  padding: 9px 18px;
  font-size: 18px;
  border: none;
  border-radius: 25px;
  color: #fff;
  cursor: pointer;
  outline: none;
  display: block;
  clear: both;
}
.form-center {
  margin-left:-25px;
}
</style>
<template>
  <div class="page-user-login" :style="{height: allHeight}">
    <div class="box">
      <h4 class="title">
        <div class="normal-title">
          <a class="active" href="javascript:;">登录</a>
          <b>·</b>
          <a @click="handleRegister()">注册</a>
        </div>
      </h4>
      <Row>
        <Form ref="account" :model="account" :rules="ruleInline" :label-width="50">
          <FormItem prop="email" label="">
            <Input type="email" v-model="account.email" placeholder="邮箱" class="form-center">
              <Icon type="ios-mail-outline" slot="prepend"></Icon>
            </Input>
          </FormItem>
          <FormItem prop="password" label="">
            <Input type="password" v-model="account.password" placeholder="密码" class="form-center">
              <Icon type="ios-lock-outline" slot="prepend"></Icon>
            </Input>
          </FormItem>
          <FormItem>
            <Button type="primary" class="sign-in-button form-center" @click="handleLogin('account')">登录</Button>
            <!-- <Button style="margin-left: 8px" @click="handleRegister()">注册</Button> -->
          </FormItem>
        </Form>
      </Row>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      allHeight: `${window.innerHeight}px`,
      account: {
        email: '',
        password: ''
      },
      ruleInline: {
        email: [
          { required: true, message: '登录邮箱不能为空', trigger: 'blur' },
          { type: 'email', min: 6, message: '邮箱格式不对', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '登录密码不能为空', trigger: 'blur' },
          { type: 'string', min: 4, message: '密码长度不少于4位数', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    handleLogin (name) {
      this.$refs[name].validate((valid) => {
        if (!valid) {
          this.$Message.error('登录参数校验失败')
          return
        }
        this.$Loading.start()
        this.$http.post(this.$const.uic.api.user_login_email, {
          email: this.account.email,
          password: this.$md5(this.account.password)
        }).then(res => {
          if (res.code !== 0) {
            this.$Loading.error()
            return
          }
          window.localStorage.setItem(this.$const.uic.local_token, res.data)
          this.$Loading.finish()
          this.$Message.success('登录成功!')
          if (this.$route.query.redirectUrl) {
            window.location = this.$route.query.redirectUrl
            return
          }
          this.$router.push('/')
        })
      })
    },
    handleRegister () {
      if (this.$route.query.redirectUrl) {
        this.$router.push(`/user/register?redirectUrl=${this.$route.query.redirectUrl}`)
        return
      }
      this.$router.push('/user/register')
    }
  },
  created () {
    document.title = `登录-${this.$const.moduleTitles.webset}`
  }
}
</script>



