<style scoped>
.markdown-body {
  z-index: 0;
}
.ivu-form-item {
  margin-bottom: 0 !important;
}
</style>
<template>
  <div class="efficient-statistics">
    <Row>
      <Col span="4">
        <Menu theme="light" active-name="1">
          <MenuGroup title="统计管理">
            <MenuItem name="1">
              <Icon type="ios-checkbox-outline" />
              任务统计
            </MenuItem>
          </MenuGroup>
        </Menu>
      </Col>
      <Col span="20" offset="0">
        <Row>
          <Col span="4">
            <DatePicker @on-change="rangeTimeChange" type="daterange" show-week-numbers placement="bottom-end" placeholder="请选择需要统计的时间范围" style="width: 200px"></DatePicker>
          </Col>
          <Col span="5">
            <Button type="primary" @click="rangeReport">时间范围查询</Button>&nbsp;&nbsp;&nbsp;&nbsp;
            <Button type="success" @click="weekReport">最新周报</Button>
          </Col>
          <Col span="15">
            <Form :label-width="120">
              <FormItem label="项目类型筛选：">
                <Select multiple @on-change="showCategoryChange" v-model="showCategorys" placeholder="筛选需要统计的项目类型" style="width:600px">
                  <Option v-for="item in projectCategorys" :value="item.value" :key="item.value">{{ item.label }}</Option>
                </Select>
              </FormItem>
            </Form>
          </Col>
        </Row>
        <Row style="margin-top:20px;">
          <mavon-editor
            :toolbars="editorSetting"
            v-model="content"
            :style="{
              minHeight: contentHeight,
              maxHeight: contentHeight,
              overflow: 'auto'}"/>
        </Row>
      </Col>
    </Row>
  </div>
</template>
<script>
export default {
  data () {
    return {
      content: ``,
      resData: {
        iterations: [],
        projects: []
      },
      startAt: '',
      endAt: '',
      showCategorys: [],
      projectCategorys: this.$const.constants.projectCategorys,
      editorSetting: {
        bold: true, // 粗体
        italic: true, // 斜体
        header: true, // 标题
        underline: true, // 下划线
        strikethrough: true, // 中划线
        mark: true, // 标记
        superscript: true, // 上角标
        subscript: true, // 下角标
        quote: true, // 引用
        ol: true, // 有序列表
        ul: true, // 无序列表
        link: true, // 链接
        // imagelink: true, // 图片链接
        code: true, // code
        table: true, // 表格
        // fullscreen: true, // 全屏编辑
        readmodel: true, // 沉浸式阅读
        // htmlcode: true, // 展示html源码
        help: true, // 帮助
        /* 1.3.5 */
        undo: true, // 上一步
        redo: true, // 下一步
        // trash: true, // 清空
        // save: true, // 保存（触发events中的save事件）
        /* 1.4.2 */
        navigation: true, // 导航目录
        /* 2.1.8 */
        alignleft: true, // 左对齐
        aligncenter: true, // 居中
        alignright: true, // 右对齐
        /* 2.2.1 */
        subfield: true, // 单双栏模式
        preview: true // 预览
      },
      contentHeight: '700px',
      changeNumber: 1
    }
  },
  methods: {
    showCategoryChange (values) {
      this.changeNumber = this.changeNumber - 1
      if (this.changeNumber === 0) {
        return
      }
      values = (values || []).map(item => item * 1)
      this.parseContent(this.resData, values)
    },
    parseContent (resData, showProjectCategorys) {
      const showCategorys = []
      let content = '# 一 项目 \n\n'
      content = this.parseItem(content, resData.projects, 1, showCategorys, showProjectCategorys)
      content += '# 二 日常 \n\n'
      content = this.parseItem(content, resData.iterations, 2, showCategorys, showProjectCategorys)
      this.content = content
      this.showCategorys = showCategorys.map(item => item + '')
    },
    parseItem (content, projects, index, showCategorys, showProjectCategorys) {
      if (!projects || projects.length === 0) {
        content += '无 \n\n'
      } else {
        let number = 1
        for (let i = 0; i < projects.length; i++) {
          if (showProjectCategorys.indexOf(projects[i].projectCategory) === -1) {
            continue
          }
          if (showCategorys.indexOf(projects[i].projectCategory) === -1) {
            showCategorys.push(projects[i].projectCategory)
          }
          content += `## ${index}.${number++} ${projects[i].projectName} \n\n`
          if (!projects[i].tasks || projects[i].tasks.length === 0) {
            content += '无 \n\n'
          } else {
            const tasks = projects[i].tasks
            for (let j = 0; j < tasks.length; j++) {
              content += `- ${tasks[j]} \n`
            }
            content += '\n'
          }
        }
      }
      return content
    },
    weekReport () {
      this.$Loading.start()
      this.$http.post(this.$const.efficient.api.task_report_week, {}).then(res => {
        if (res.code !== 0) {
          this.$Loading.error()
          return
        }
        this.$Loading.finish()
        this.resData = res.data
        this.parseContent(this.resData, this.projectCategorys.map(item => item.value * 1))
      })
    },
    rangeTimeChange (dates) {
      this.startAt = !dates[0] ? '' : Date.parse(new Date(dates[0]))
      this.endAt = !dates[1] ? '' : Date.parse(new Date(dates[1] + ' 23:59:59'))
    },
    rangeReport () {
      if (!this.startAt || !this.endAt) {
        this.$Message.error('请选择需要查询的时间范围')
        return
      }
      this.$Loading.start()
      this.$http.post(this.$const.efficient.api.task_report_range, {
        startAt: this.startAt,
        endAt: this.endAt
      }).then(res => {
        if (res.code !== 0) {
          this.$Loading.error()
          return
        }
        this.$Loading.finish()
        this.resData = res.data
        this.parseContent(this.resData, this.projectCategorys.map(item => item.value * 1))
      })
    }
  },
  created () {
    this.weekReport()
    let containerHeight = window.localStorage.getItem('containerHeight')
    this.contentHeight = containerHeight - 52 + 'px'
  }
}
</script>



